.hackathonAroundYou {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
}
.beapartTitle {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.header {
  flex-direction: row;
  align-items: center;
  padding: var(--padding-5xl);
}
.columns,
.hackathonAroundYouHome,
.header,
.heading3Columns {
  align-self: stretch;
  display: flex;
  justify-content: center;
}
.columns {
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  padding: var(--padding-21xl);
  gap: var(--gap-xl);
}
.hackathonAroundYouHome,
.heading3Columns {
  flex-direction: column;
  align-items: flex-start;
}
.hackathonAroundYouHome {
  background-color: var(--color-gray);
  overflow: hidden;
  align-items: center;
  text-align: center;
  font-size: var(--q-h2-size);
  color: var(--color-white);
  font-family: var(--q-h4);
}
@media screen and (max-width: 960px) {
  .columns {
    flex-direction: column;
    gap: var(--gap-10xl);
    align-items: center;
    justify-content: center;
  }
}
@media screen and (max-width: 420px) {
  .columns {
    flex-direction: column;
    gap: var(--gap-11xl);
    align-items: center;
    justify-content: center;
  }
}
