.registerForThe {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
}
.beapartTitle {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.header,
.heading3Columns,
.registrationHackathonPage {
  align-self: stretch;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header {
  flex-direction: row;
  padding: var(--padding-5xl);
}
.heading3Columns,
.registrationHackathonPage {
  flex-direction: column;
}
.heading3Columns {
  flex: 1;
  padding: 0 var(--padding-81xl);
}
.registrationHackathonPage {
  background-color: var(--color-gray);
  height: 735.5px;
  overflow: hidden;
  flex-shrink: 0;
  padding: var(--padding-xl) 0;
  box-sizing: border-box;
  text-align: center;
  font-size: var(--q-h2-size);
  color: var(--color-white);
  font-family: var(--q-h4);
}
