.about {
  position: relative;
}
.navbarLink {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-5xl) var(--padding-xs);
}
.navbarButton,
.navbarMenu {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.navbarButton {
  border-radius: var(--br-8xs);
  background-color: var(--b9);
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
  justify-content: center;
  padding: var(--padding-5xs) var(--padding-xl);
}
.navbarMenu {
  overflow: hidden;
  justify-content: flex-start;
  gap: var(--gap-13xl);
  text-align: left;
  font-size: var(--q-body-size);
  color: var(--color-white);
  font-family: var(--q-h4);
}
@media screen and (max-width: 960px) {
  .navbarMenu {
    display: none;
  }
}
