.problemStatements {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
}
.beapartTitle,
.header {
  align-items: center;
  justify-content: center;
}
.beapartTitle {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.header {
  flex-direction: row;
  padding: var(--padding-5xl);
}
.columns,
.header,
.heading3Columns {
  align-self: stretch;
  display: flex;
}
.columns {
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: var(--padding-21xl);
}
.heading3Columns {
  flex: 1;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.hackathonPage,
.problemStatementHackathon {
  background-color: var(--color-gray);
  overflow: hidden;
  display: flex;
}
.problemStatementHackathon {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: center;
  font-size: var(--q-h2-size);
  color: var(--color-white);
  font-family: var(--q-h4);
}
.hackathonPage {
  position: relative;
  width: 100%;
  height: 1594.5px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 420px) {
  .columns {
    flex-direction: row;
    gap: var(--gap-21xl);
    align-items: center;
    justify-content: center;
  }
}
