.problemSkills {
  color: var(--color-fuchsia);
}
.h1TitleCopyGoesContainer {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
}
.organizeAHackathon {
  color: var(--b9);
}
.withOtherTechpreneurs {
  color: var(--color-white);
}
.h1TitleCopyGoesContainer2 {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  color: var(--color-fuchsia);
}
.column,
.content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content {
  align-self: stretch;
  gap: var(--gap-3xs);
}
.column {
  flex: 1;
}
.container,
.h1TitleCopyGoes {
  align-self: stretch;
  display: flex;
  align-items: center;
}
.container {
  flex-direction: row;
  justify-content: flex-start;
  padding: 0 var(--padding-21xl);
}
.h1TitleCopyGoes {
  margin: 0;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  width: 46px;
  flex-shrink: 0;
}
.contentChild {
  position: relative;
  width: 250px;
  height: 100px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.column1,
.content1 {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}
.content1 {
  gap: var(--gap-5xl);
}
.column1 {
  height: 100px;
}
.container1,
.heroContentWrapper {
  align-self: stretch;
  display: flex;
  align-items: center;
}
.container1 {
  flex-direction: row;
  justify-content: flex-end;
  padding: 0 var(--padding-21xl);
  color: var(--color-fuchsia);
}
.heroContentWrapper {
  height: 600px;
  overflow: hidden;
  flex-shrink: 0;
  flex-direction: column;
  justify-content: center;
  gap: var(--gap-5xs);
  text-align: left;
  font-size: var(--q-h1-size);
  color: var(--color-white);
  font-family: var(--q-h4);
}
